import { Component, OnInit, Input } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { UserService } from '../../../../api/services/user.service';
import { VehicleService } from '../../../../api/services/vehicle.service';
import { ApartmentService } from '../../../../api/services/apartment.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-profile-vehicles',
  templateUrl: './profile-vehicles.component.html',
  styleUrls: ['./profile-vehicles.component.scss']
})
export class ProfileVehiclesComponent implements OnInit {
  
  isUserDataLoaded:boolean = false;
  isUnitSelected:boolean = false;

  blockData:any;
  blockUnitData:any;
  unitTypeData:any;

  userUnitId:string = "";

  unitListData:any;
  unitNameList:any;

  isError:boolean = false;
  errorMessage:string = "";

  vehicleDataList:any;
  vehicle:any;

  vehicleTypeList:any;

  isVehicleAvailable:boolean = false;

  @Input() userId?:any;
  
  parkingSlotsDataList:any;

  constructor(
  	private router: Router,
    private route: ActivatedRoute,
    private userService:UserService,
    private vehicleService:VehicleService,
  	private apartmentService:ApartmentService,
  	private sharedService:SharedService,
  	private lookupService:LookupService,
  	private cookieService:CookieService
  ) { }

  showUnitDetails(id){


    this.isUserDataLoaded = false;
    this.isUnitSelected = true;

    this.vehicleService.getAllVehiclesByApartmentBlockUnitId(parseInt(id)).subscribe((res:any) => {

      this.vehicleDataList = res;
      this.isUserDataLoaded = true;      

    })

    this.vehicleService.getParkingSlotByApartmentIdUnitId(parseInt(id)).subscribe((res:any) => {

      this.parkingSlotsDataList = res;

    });


  }

  getVehicleType(id){

    var data = _.filter( this.vehicleTypeList, function( item ) {
      
      if(item.lookupValueId === id){
        return item;
      }

    });


    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName;
    }

  }

  getUnitName(id){

    var data = _.filter( this.unitNameList, function( item ) {
      
      if(item.apartmentBlockUnitId === id){
        return item;
      }

    });


    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].apartmentBlockUnitNumber;
    }

  }

  getParams(event){

    if(event){

      this.vehicleService.getAllVehiclesByApartmentBlockUnitId(parseInt(this.userUnitId)).subscribe((res:any) => {

        this.vehicleDataList = res;
        this.isUserDataLoaded = true;      

      })

    }

  }

  isPlotNotAvailable(){
    return this.parkingSlotsDataList == 0 ? true : false;
  }

  isVehicleNotAvailable(){
    return this.vehicleDataList == 0 ? true : false;
  }
  

  ngOnInit() {


    if(this.route.params['value'].id != undefined){
      this.userId = this.route.params['value'].id
    }
    else {
      this.userId = parseInt(this.cookieService.get('userId'))
    }

    this.unitNameList = [];

  	this.apartmentService.getApartmentBlockByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {
      this.blockData = res;
    });

    this.lookupService.getLookupValueByLookupTypeId(2).subscribe((res:any) => {

      this.vehicleTypeList = res;

    });

    

    this.lookupService.getLookupValueByLookupTypeId(1).subscribe((res:any) => {
    	
    	this.unitTypeData = res;

      
      this.apartmentService.getAllApartmentBlockUnitUsersByUserId(this.userId).subscribe((unit:any) => {

        this.unitListData = unit;



        _.each(this.unitListData, item => {

          this.apartmentService.getApartmentBlockUnitById(item.apartmentBlockUnitId).subscribe((data:any) => {


            this.unitNameList.push(data[0]);
            this.isUserDataLoaded = true;


          },
          error => {

          });

        })

      },
      error => {
        
      }); 

    	
    });


  

  }

}
